import React from 'react'
import styled from 'styled-components'
import { MOBILE_SCREEN } from '../../styles/globalVar'

const Hero = ({ imgSrc, title, drophead, opacity }) => {
	return (
		<HeroImage imgSrc={imgSrc} opacity={opacity}>
			<ServiceTitle>
				<p>{title}</p>
				<p>{drophead}</p>
			</ServiceTitle>
		</HeroImage>
	)
}

export default Hero

const HeroImage = styled.div`
	background-image: 
	linear-gradient(to right, rgba(0,0,0,${props => props.opacity}), rgba(0, 0, 0, 0)),
	url(${props => props.imgSrc});
	width: 100vw;
	max-width:100%;
	position: relative;
	height: 100vh;
	top: -56px;
	background-position: center;
	background-size: cover ;
	object-fit: cover;
`

const ServiceTitle = styled.div`
	position: absolute;
	top: 50vh;
	left: 20%;
	color: white;
	> * {
	&:nth-child(1){
		font-size: 3.5rem;
		font-weight: 700;
		@media only screen and (max-width: ${MOBILE_SCREEN}) {
		font-size: 2.5rem;
		}
	}
	&:nth-child(2){
		font-size: 1.25rem;
		@media only screen and (max-width: ${MOBILE_SCREEN}) {
		font-size: 1rem;
		}
	}
}
	@media only screen and (max-width: ${MOBILE_SCREEN}) {
		left: 60px;
	}
`
