import React, { useEffect, useState } from 'react'
import { useTranslation } from 'react-i18next'
import styled from 'styled-components'
import { MAX_WIDTH, MOBILE_SCREEN } from '../../styles/globalVar'
import SupportTemplate from '../../component/templates/SupportTemplate'
import Hero from '../../component/templates/Hero'
import { Body, Button, ButtonWrap, Headline, Section } from '../../component/templates/StyledTemplates'
import ScreenTemplate from '../../component/templates/ScreenTemplate'
import i18next from 'i18next'
import { Color } from '../../styles/Themes'
import screenDouble from '../../assets/aj21/screenDouble.png'
import screenDoubleJP from '../../assets/aj21/screenDoubleJP.png'
import screenHit from '../../assets/aj21/screenHit.png'
import screenHitJP from '../../assets/aj21/screenHitJP.png'
import screenStand from '../../assets/aj21/screenStand.png'
import screenStandJP from '../../assets/aj21/screenStandJP.png'
import aj21hero from '../../assets/aj21/aj21hero.png'

const AJ21 = () => {
	const { t } = useTranslation()
	const locale = i18next.language

	const [screenshotHit, setScreenshotHit] = useState()
	const [screenshotStand, setScreenshotStand] = useState()
	const [screenshotDouble, setScreenshotDouble] = useState()

	useEffect(() => {
		switch (locale) {
			case 'ko':
				setScreenshotHit(screenHit)
				setScreenshotStand(screenStand)
				setScreenshotDouble(screenDouble)
				break;
			case 'en':
				setScreenshotHit(screenHit)
				setScreenshotStand(screenStand)
				setScreenshotDouble(screenDouble)
				break;
			case 'ja':
				setScreenshotHit(screenHitJP)
				setScreenshotStand(screenStandJP)
				setScreenshotDouble(screenDoubleJP)
				break
			default:
		}
	}, [locale])

	const GoToNotify = () => {
		window.open('https://brawny-jester-77b.notion.site/27f8a380f66644d083c49a3098458719')
	}

	return (
		<>
			<Hero
				imgSrc={aj21hero}
				title={'AJ21'}
				drophead={'Blackjack card game'}
				opacity={0.5}
			/>
			<AppDesc>
				<img src={require('../../assets/aj21/icon.png')} style={styles.img} alt='ajicon' />
				<AppDescText>
					{t('AJ21.appDesc')}
				</AppDescText>
			</AppDesc>
			{/* <BoxCopy>
				<p>{t('AJ21.boxCopy1')}</p>
			</BoxCopy> */}
			<Section>
				<ScreenTemplate
					title={t('AJ21.screen1title')}
					content={t('AJ21.screen1content')}
					screenshot={screenshotHit}
				/>
				<ScreenTemplate
					title={t('AJ21.screen2title')}
					content={t('AJ21.screen2content')}
					screenshot={screenshotStand}
				/>
				<ScreenTemplate
					title={t('AJ21.screen3title')}
					content={t('AJ21.screen3content')}
					screenshot={screenshotDouble}
				/>
			</Section>
			<Section>
				<Headline>{t('Common.download')}</Headline>
				<Body>{t('AJ21.downloadBody')}</Body>
				{/* <DownloadGuide><p>{t('AJ21.downloadGuide')}</p></DownloadGuide>
				<ButtonWrap>
					<Button>{t('Common.download')}</Button>
				</ButtonWrap> */}
			</Section>
			<Section>
				<Headline>{t('Common.moreInfo')}</Headline>
				<Body>{t('AJ21.moreInfoBody')}</Body>
				<ButtonWrap>
					<Button onClick={GoToNotify}>{t('Common.new')}</Button>
				</ButtonWrap>
			</Section>
			<SupportTemplate />
		</>
	)
}

export default AJ21

const AppDesc = styled.div`
	margin: 0 auto;
	text-align: center;
	height: 450px;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	background-color: #282828;
	@media only screen and (max-width: ${MOBILE_SCREEN}) {
		margin-bottom: 150px;
		padding: 0px 24px;
	}
`

const AppDescText = styled.div`
	max-width: ${MAX_WIDTH};
	/* width: 100%; */
	color: white;
	width: 100%;
	text-align: center;
	font-size: 1.5rem;
	@media only screen and (max-width: ${MOBILE_SCREEN}) {
		text-align: center;
		font-size: 1.25rem;
	}
`

const DownloadGuide = styled.div`
	display: flex;
	justify-content: center;
	align-items: center;
	text-align: center;
	> * {
	&:nth-child(1){
		background-color: ${Color.G100};
		font-size: 1.25rem;
		width: 100%;
		padding: 24px;
		margin-bottom: 40px;
		white-space: pre-wrap;
		@media only screen and (max-width: ${MOBILE_SCREEN}) {
			font-size: 0.9rem;
			padding: 16px;
			}
		}
	}
`

const styles = {
	img: {
		width: 200,
		marginBottom: 50
	}
}