import React, { useEffect } from 'react'
import styled from 'styled-components'
import { MOBILE_SCREEN } from '../../styles/globalVar'
import appStoreBanner from '../../assets/appstorebanner.png'
import playStoreBanner from '../../assets/playstorebanner.png'

const AppleBanner = ({ link }) => {
	const storeBannerClick = (link) => {
		window.open(link)
	}

	return (
		<StoreBanner img={appStoreBanner} onClick={() => {
			storeBannerClick(link)
		}} />
	)
}

const GoogleBanner = ({ link }) => {
	const storeBannerClick = (link) => {
		window.open(link)
	}

	return (
		<StoreBanner img={playStoreBanner} onClick={() => {
			storeBannerClick(link)
		}} />
	)
}

export {
	AppleBanner,
	GoogleBanner,
}

const StoreBanner = styled.button`
	background-image: url(${props => props.img});
	background-repeat: no-repeat;
	background-position: center;
	background-size: contain;
	background-color: white;
	cursor: pointer;
	width: 236px;
	height: 70px;
	border: 0px;
	margin: 0px 10px;
	@media only screen and (max-width: ${MOBILE_SCREEN}) {
		width: 189px;
		height: 56px;
		margin-bottom: 20px;
	}
`
