import React, { useState, useRef, useEffect } from 'react'
import styled from 'styled-components'
import { Link } from 'react-router-dom'
import { useTranslation } from "react-i18next"
import { MAX_WIDTH, MOBILE_SCREEN } from '../../styles/globalVar'
import { ReactComponent as Logo } from '../../assets/raplop.svg'
import { ReactComponent as MobileMenuIcon } from '../../assets/mobileMenu.svg'

const Header = () => {
	const { t, i18n } = useTranslation()
	const [open, setOpen] = useState()

	const settingKR = () => { i18n.changeLanguage('ko') }
	const settingJP = () => { i18n.changeLanguage('ja') }
	const settingEN = () => { i18n.changeLanguage('en') }
	const clickHbg = () => {
		setOpen(open === true ? false : true)
	}

	return (
		<NavBar>
			<Wrap>
				<Link to='/' onClick={() => { setOpen(false) }}>
					<Logo width={60} style={{ marginTop: 6 }} />
				</Link>
				<MenuWrap>
					<Menu to='/about' >About</Menu>
					<Menu to='/outputs' >Outputs</Menu>
				</MenuWrap>
				<LngWrap>
					<LngButton onClick={settingKR}></LngButton>
					<LngButton onClick={settingJP} ></LngButton>
					<LngButton onClick={settingEN} >A</LngButton>
				</LngWrap>
				<Hbg open={open} onClick={clickHbg}>
					<MobileMenuIcon width={26} />
				</Hbg>
			</Wrap>
			{open === true &&
				<MobileMenu onClick={() => { setOpen(false) }}>
					<Upper>
						<Menu to='/about' >About</Menu>
						<Menu to='/outputs' >Outputs</Menu>
					</Upper>
					<Under>
						<LngButton onClick={settingKR}></LngButton>
						<LngButton onClick={settingJP}></LngButton>
						<LngButton onClick={settingEN}>A</LngButton>
					</Under>
				</MobileMenu>
			}
		</NavBar>
	)
}

export default Header

const NavBar = styled.div`
	height: 56px;
	width: 100vw;
	position: sticky;
	background-color: rgba(255,255,255,1);
	top: 0;
	z-index: 9999;
	@media only screen and (max-width: ${MOBILE_SCREEN}) {
		height: 50px;
	}
`
const Wrap = styled.div`
	max-width: ${MAX_WIDTH};
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: space-between;
	margin: auto;
	height: 100%;
	@media only screen and (max-width: ${MOBILE_SCREEN}) {
		padding: 0px 24px
	}
	@media only screen and (max-width: 1000px) {
		padding: 0px 24px
	}
`
const Menu = styled(Link)`
	color: black;
	font-size: 1rem;
	text-decoration: none;
	cursor: pointer;
	text-align: center;
	margin: 0px 40px;
	@media only screen and (max-width: ${MOBILE_SCREEN}) {
		font-size: 1.5rem;
		margin-top: 2rem;
	}
`

const MenuWrap = styled.div`
	@media only screen and (max-width: ${MOBILE_SCREEN}) {
		display: none;
	}
`

const LngButton = styled.div`
	background-color: white;
	width: 40px;
	text-align: center;
	cursor: pointer;
	margin: 0.2rem;
	border-radius: 8px;
	@media only screen and (max-width: ${MOBILE_SCREEN}) {
		font-size: 1rem;
		margin: 0.5rem;
	}
`
const Hbg = styled.div`
	display: none;
	width: 40px;
	height: 40px;
	@media only screen and (max-width: ${MOBILE_SCREEN}) {
		display: flex;
		justify-content: center;
		align-items: center;
	}
`
const LngWrap = styled.div`
	display: flex;
	@media only screen and (max-width: ${MOBILE_SCREEN}) {
		display: none;
	}
`
const MobileMenu = styled.div`
	display: none;
	@media only screen and (max-width: ${MOBILE_SCREEN}) {
		position: absolute;
		display: flex;
		flex-direction: column;
		width: 100%;
		height: 100vh;
		max-height: ${window.innerHeight};
		background-color: rgba(255,255,255,1);
	}
`
const Upper = styled.div`
	display: flex;
	flex-direction: column;
	justify-content: center;
	text-align: center;
	height: 50%;
`

const Under = styled.div`
	display: flex;
	align-items: center;
	justify-content: center;
	/* height: 50%; */
`