import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import Hero from "../../component/templates/Hero";
import { Button, Section } from "../../component/templates/StyledTemplates";
import styled from 'styled-components'
import ScreenTemplate from "../../component/templates/ScreenTemplate";
import golfhero from '../../assets/golfhero.jpg'
import listJP from '../../assets/goalf/listJP.png'
import { Headline, Body, ButtonWrap } from "../../component/templates/StyledTemplates";
import SupportTemplate from "../../component/templates/SupportTemplate";
import { AppleBanner, GoogleBanner } from '../../component/comps/StoreBanner'
import i18next from "i18next";
import boxCopy1 from '../../assets/goalf/boxCopy1.png'
import { MAX_WIDTH, MOBILE_SCREEN } from "../../styles/globalVar";
import { Color } from '../../styles/Themes'
import screen1JP from '../../assets/goalf/screen1JP.png'
import screen2JP from '../../assets/goalf/screen2JP.png'
import screen3JP from '../../assets/goalf/screen3JP.png'
import screen4JP from '../../assets/goalf/screen4JP.png'
import screen5JP from '../../assets/goalf/screen5JP.png'
import screen1KR from '../../assets/goalf/screen1KR.png'
import screen2KR from '../../assets/goalf/screen2KR.png'
import screen3KR from '../../assets/goalf/screen3KR.png'
import screen4KR from '../../assets/goalf/screen4KR.png'
import screen5KR from '../../assets/goalf/screen5KR.png'
import test from '../../assets/goalf/test.png'


const Goalf = () => {
	const { t } = useTranslation()
	const locale = i18next.language
	const [screenshot1, setScreenshot1] = useState()
	const [screenshot2, setScreenshot2] = useState()
	const [screenshot3, setScreenshot3] = useState()
	const [screenshot4, setScreenshot4] = useState()
	const [screenshot5, setScreenshot5] = useState()

	const GoToPrivacyKR = () => {
		window.open('https://nuoyeat.github.io/raplop-policy/goalf/privacypolicyKR.html')
	}
	const GoToPrivacyJP = () => {
		window.open('https://nuoyeat.github.io/raplop-policy/goalf/privacypolicyJP.html')
	}
	const GoToTOCKR = () => {
		window.open('https://nuoyeat.github.io/raplop-policy/goalf/termsofuseKR.html')
	}
	const GoToTOCJP = () => {
		window.open('https://nuoyeat.github.io/raplop-policy/goalf/termsofuseJP.html')
	}
	const GoToNotify = () => {
		window.open('https://brawny-jester-77b.notion.site/8f2a64abb632492ea54f46eab7da9e8b?v=63dcca362e8740beb482474c27bb1441')
	}

	useEffect(() => {
		switch (locale) {
			case 'ko':
				setScreenshot1(screen1KR)
				setScreenshot2(screen2KR)
				setScreenshot3(screen3KR)
				setScreenshot4(screen4KR)
				setScreenshot5(screen5KR)
				break;
			case 'en':
				setScreenshot1(screen1KR)
				setScreenshot2(screen2KR)
				setScreenshot3(screen3KR)
				setScreenshot4(screen4KR)
				setScreenshot5(screen5KR)
				break;
			case 'ja':
				setScreenshot1(screen1JP)
				setScreenshot2(screen2JP)
				setScreenshot3(screen3JP)
				setScreenshot4(screen4JP)
				setScreenshot5(screen5JP)
				break
			default:
		}
	}, [locale])

	return (
		<>
			<Hero title={'Goalf'} drophead={'Golf scorecard'} imgSrc={test} opacity={0.5} />
			<BoxCopy>
				<BoxCopyText>
					{t('Goalf.appDesc')}
				</BoxCopyText>
			</BoxCopy>
			<Section>
				<ScreenTemplate
					screenshot={screenshot1}
					title={t('Goalf.screen1title')}
					content={t('Goalf.screen1content')}
				/>
				<ScreenTemplate
					screenshot={screenshot2}
					title={t('Goalf.screen2title')}
					content={t('Goalf.screen2content')}
				/>
				<ScreenTemplate
					screenshot={screenshot3}
					title={t('Goalf.screen3title')}
					content={t('Goalf.screen3content')}
				/>
				<ScreenTemplate
					screenshot={screenshot4}
					title={t('Goalf.screen4title')}
					content={t('Goalf.screen4content')}
				/>
				<ScreenTemplate
					screenshot={screenshot5}
					title={t('Goalf.screen5title')}
					content={t('Goalf.screen4content')}
				/>
			</Section>
			<Section>
				<Headline>{t('Common.download')}</Headline>
				<Body>{t('Goalf.downloadBody')}</Body>
				<ButtonWrap>
					<AppleBanner link={'https://apps.apple.com/kr/app/goalf/id1617019093'} />
					<GoogleBanner link={'https://play.google.com/store/apps/details?id=com.goalf.goalf'} />
				</ButtonWrap>
			</Section>
			<Section>
				<Headline>{t('Common.moreInfo')}</Headline>
				<ButtonWrap>
					<Button onClick={locale === 'ja' ? GoToPrivacyJP : GoToPrivacyKR}>Privacy Policy</Button>
					<Button onClick={locale === 'ja' ? GoToTOCJP : GoToTOCKR}>Terms of Use</Button>
					<Button onClick={GoToNotify}>What's New</Button>
				</ButtonWrap>
			</Section>
			<SupportTemplate />
		</>
	)
}

export default Goalf

const BoxCopy = styled.div`
	margin: 0 auto;
	text-align: center;
	background-image: 
	linear-gradient(to right, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0)),
	url(${boxCopy1});
	background-position: center;
	background-size: cover;
	object-fit: cover;
	height: 450px;
	display: flex;
	justify-content: center;
	align-items: center;
	@media only screen and (max-width: ${MOBILE_SCREEN}) {
		margin-bottom: 150px;
	}
`

const BoxCopyText = styled.div`
	max-width: ${MAX_WIDTH};
	/* width: 100%; */
	color: white;
	width: 100%;
	text-align: left;
	font-size: 1.5rem;
	font-weight: 600;
	white-space: pre-wrap;
	@media only screen and (max-width: ${MOBILE_SCREEN}) {
		text-align: center;
		font-size: 1.25rem;
	}
	@media only screen and (max-width: 1000px) {
		padding: 0px 24px
	}
`